<script>
import {fetchListLevel,fetchListChildrenLevel} from '@/api/productCate'
const defaultListQuery = {
    // keyword: null,
    pageNum: 1,
    pageSize: 5,
    productName:null, //åååç§°
    publishStatus: null,  //ä¸ä¸æ¶ç¶æ
    auditStatus: null,  //å®¡æ ¸ç¶æ
    productCode: null,  //ååç¼å·
    // sale:null,
    // pic:null,
    // price:null,
    // productCategoryId: null,
    // brandId: null
  };

export default {
    data(){
      return{
        productCateOptions:[],
        listQuery: Object.assign({}, defaultListQuery),
        operateType:null,
        publishStatusOptions:[
          {
            label:"ä¸æ¶",
            value:0
          },
