            label:"ä¸æ¶",
            value:1
          }
        ],
        verifyStatusOptions:[
        {
          label:"å·²éè¿",
          value:0
        },
        {
          label:"æªéè¿",
          value:1
        }
        ],
        operates:[
          {
            label:'å¨é',
            value:0
          },
