            label:'å¨é¨æ´æ¹',
            value:1
          }
        ],
        list: [
          {
            productName:'å¸½å­',
            publishStatus:'ä¸æ¶',
            auditStatus:'å®¡æ ¸',
            productCode:'1212324',
            pic:'æ ',
